/* Media Queries for custom slider */
@media screen and (max-width: 660px) {
  .demo-1 .deco {
    width: 130px;
    height: 130px;
    margin-left: -65px;
    margin-bottom: 50px;
  }
  .demo-1 [data-icon]:after {
    width: 110px;
    height: 110px;
    line-height: 110px;
    font-size: 40px;
    margin: -55px 0 0 -55px;
  }
  .demo-1 .sl-slide blockquote {
    margin-bottom: -120px;
  }
  .demo-1 .sl-slide h2 {
    line-height: 22px;
    font-size: 18px;
    margin-bottom: -40px;
    letter-spacing: 8px;
  }
  .demo-1 .sl-slide blockquote p:before {
    line-height: 10px;
    width: 40px;
    height: 40px;
    font-size: 120px;
    left: -45px;
  }
  .demo-2 .sl-slider-wrapper {
    height: 500px;
  }
  .demo-2 .sl-slider h2 {
    font-size: 36px;
  }
  .demo-2 .sl-slider blockquote {
    font-size: 16px;
  }
}
