/* GENERAL STYLE */

.device-mockup {
  position: relative;
  width: 100%;
  padding-bottom: 61.775701%;
}

.device-mockup > .device {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background: url("/images/website/devices/macbook.png") no-repeat;
}

.device-mockup > .device > .screen {
  position: absolute;
  top: 11.0438729%;
  bottom: 14.6747352%;
  left: 13.364486%;
  right: 13.364486%;
  overflow: hidden;
}

.device-mockup > .device > .button {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  overflow: hidden;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  cursor: pointer;
}

