/* 01. Typography
   ---------------------------------------------------------------------------*/
body {
    font-family: 'omnes-pro', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
dl.metadata dt,
.contact-form label,
.price-shop,
blockquote .bold-quote {
    font-family: 'omnes-pro', sans-serif;
}

h1 {
    font-size: 4em;
}

h2 {
    font-size: 3.3em;
    line-height: 1.2em;
}

h3 {
    font-size: 2.3em;
}

h4 {
    font-size: 1.8em;
}

h5,
blockquote .bold-quote {
    font-size: 1.3em;
}

h6 {
    font-size: 1.1em;
}

p {
    line-height: 1.6em;
}

p:last-child {
    margin-bottom: 0;
}

p.lead {
    font-size: 1.2em;
}

p.lead.x2 {
    font-size: 1.7em;
    font-weight: 200;
}

p.lead:last-child {
    margin-bottom: 0;
}

blockquote {
    padding: 20px;
}

blockquote .bold-quote {
    padding-top: 0;
}

blockquote .bold-quote {
    margin-bottom: .5em;
}

blockquote.theme {
    border: none;
}

.upper {
    text-transform: uppercase;
}

/* 02. General Rules
   ---------------------------------------------------------------------------*/
html,
body {
    width: 100%;
    height: 100%;
}

body {
    overflow-x: hidden;
}

body .page {
    position: relative;
    height: 100%;
}

body.boxed-layout .page {
    height: auto;
    margin: 0 auto;
}

body.boxed-layout .page .demo-1 .sl-slider-wrapper {
    height: 600px;
    overflow: hidden;
    position: relative;
}

a {
    transition: all 0.5s ease 0s;
}

a:active,
a:focus,
a:hover {
    text-decoration: none;
    outline: none;
}

iframe {
    border: none;
}

.lighter,
.darker {
    opacity: 0.8;
    filter: alpha(opacity=80);
}

.slide-container {
    position: relative;
    width: 100%;
    height: 100%;
    display: table;
}

.slide-container .slide-content {
    display: table-cell;
    vertical-align: middle;
    padding: 0 10%;
}

.demo-1 .sl-slide .deco,
.demo-2 .sl-slide .deco {
    width: 200px;
    height: 200px;
    border: 0;
    border-radius: 0;
    margin: 0 0 20px 0;
    position: relative;
    left: auto;
    bottom: auto;
}

.demo-1 .sl-slide .main-title,
.demo-2 .sl-slide .main-title {
    letter-spacing: 0;
    font-weight: 400;
    text-transform: none;
    font-size: 3.4em;
    text-shadow: none;
    margin-bottom: 20px;
    padding: 0;
    position: relative;
}

.demo-1 .sl-slide blockquote,
.demo-2 .sl-slide blockquote {
    border: 0;
    height: auto;
    margin: 0;
    position: relative;
    font-size: 1em;
    padding: 0;
}

.demo-1 .sl-slide blockquote p,
.demo-2 .sl-slide blockquote p {
    font-size: 2em;
    margin-bottom: 40px;
}

.demo-1 .sl-slide blockquote p:before,
.demo-2 .sl-slide blockquote p:before {
    content: "";
}

.demo-1 .sl-slide .btn,
.demo-2 .sl-slide .btn {
    margin: 0 25px;
}

.demo-2 .sl-slider h2 {
    font-size: 70px;
    padding-top: 0;
}

.demo-2 .sl-slider blockquote {
    padding-top: 0;
    margin: auto;
}

.video-background-content .section-shade {
    position: relative;
}

.swiper-wrapper {
    height: auto !important;
}

.swiper-slide {
    width: 380px;
}

.owl-theme .owl-controls {
    margin-top: 25px;
}

.owl-theme .owl-controls .owl-page span {
    width: 10px;
    height: 10px;
    opacity: 0.2;
    filter: alpha(opacity=20);
}

.owl-theme .owl-controls .owl-page.active span {
    opacity: 0.8;
    filter: alpha(opacity=80);
}

.page-title-line h1,
.page-title-line h2,
.page-title-line h3,
.page-title-line h4,
.page-title-line h5,
.page-title-line h6 {
    margin: 0;
}

.page-title-line .order-content .order-title {
    margin: 0 20px 0 0;
}

.page-title-line .filter-content {
    margin: 0 25px 0 0;
}

.page-title-line .filter-content form .filter_price_content {
    float: left;
    width: 230px;
    margin: 0 20px 0 0;
}

.page-title-line .filter-content form .filter_price_content .filter-title {
    margin: 0 0 4px 0;
}

.filter-button {
    border-radius: 5px;
}

#home {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0;
}

.bordered-right {
    padding-right: 40px;
}

.bordered-left {
    padding-left: 40px;
}

.nav-arrows span {
    z-index: 2;
}

.sl-slide-inner {
    background-position: 50% 50%;

}

.sl-slide-horizontal .sl-slide-inner {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0)), url('/images/website/homebackground.jpg') center center no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

section {
    position: relative;
}

.section-title .icon-box,
.section-title h1,
.section-title h2,
.section-title h3,
.section-title h4,
.section-title h5,
.section-title h6 {
    margin: 0 0 30px 0;
}

.section-title .small-space {
    margin: 0 0 12px 0;
}

.section-gray {
    box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.3) inset;
}

.section-gray .indicator .indicator-line {
