}

.col-content {
    margin: 0 0 20px 0;
    padding: 10px 3px;
    font-size: 90%;
    text-align: center;
}

.call-to-action .action-title {
    margin: 16px 0;
    padding: 0;
}

.fading-title {
    overflow: hidden;
}

.fading-title .section-title {
    margin-top: 0;
    margin-bottom: 0;
