/* 05. Buttons
   ---------------------------------------------------------------------------*/
#filters .btn {
    border-color: transparent;
    box-shadow: none;
    background: none;
    font-weight: 600;
}

#filters .btn:focus {
    outline: none;
}

.button-group .btn {
    margin: 0 10px 0 0;
}

.btn {
    padding: 10px 40px;
    border-radius: 40px;
    border: 2px solid;
}

.btn-lg {
    padding: 12px 48px;
}

.btn-sm {
    padding: 8px 25px;
}

.btn-xs {
    padding: 5px 20px;
    font-size: 0.9em;
}

.btn-link {
    border-color: transparent;
}

.btn-group > .btn.btn-bordered:first-child {
    border-right: 0;
}

.btn-group > .btn.btn-bordered:last-child {
    border-left: 0;
}

.content-custom-button {
    padding: 25px;
}

/* 06. UI Elements
   ---------------------------------------------------------------------------*/
/* 06.1 Bootstrap-Touchspin
   ---------------------------------------------------------------------------*/
.bootstrap-touchspin .btn {
    padding: 6px 10px;
    font-size: 20px;
}

.bootstrap-touchspin input.qty {
    height: 42px;
    width: 40px;
    text-align: center;
    box-shadow: none;
    border-width: 1px 0;
    padding: 0;
    font-size: 1.1em;
}

.bootstrap-touchspin .input-group-addon,
.bootstrap-touchspin .input-group-btn {
    width: auto;
}

/* 06.2 Progress Bar
   ---------------------------------------------------------------------------*/
.progress {
    height: 32px;
    box-shadow: none;
}

.progress .progress-bar {
    text-align: left;
    padding: 0 0 0 13px;
    line-height: 32px;
    box-shadow: none;
}

.progress.minimal {
    margin-bottom: 7px;
    background: none;
}

.indicator {
    position: relative;
    overflow: hidden;
    margin-top: 12px;
}

.indicator .indicator-line {
    display: block;
    height: 10px;
    background-size: 9.99% 10px;
    background-position: 0 0;
}

.indicator .middle {
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
}

/* 06.3 EasyPieCharts
   ---------------------------------------------------------------------------*/
.chart {
    position: relative;
    margin-bottom: 20px;
}

.chart span {
    font-size: 5.9em;
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    margin: -62px 0 0 0;
    font-weight: 300;
}

.chart b {
    font-weight: 300;
}

.chart em {
    font-size: 0.3em;
    font-style: normal;
    vertical-align: middle;
}

/* 06.4 Counter
   ---------------------------------------------------------------------------*/
.timer {
    font-size: 4.5em;
    font-weight: 600;
}

.timer-title {
    margin: 0;
}

/* 06.5 Accordion
   ---------------------------------------------------------------------------*/
.accordion-group .accordion-item {
    position: relative;
    padding: 20px 0 20px 40px;
    border-radius: 0;
    box-shadow: none;
    margin: 0;
}

.accordion-group .accordion-item .accordion-heading {
    font-size: 1.1em;
}

.accordion-group .accordion-item .accordion-icon {
    border-radius: 20px;
    float: left;
    line-height: 23px;
    margin-left: -35px;
    margin-top: -2px;
    padding-top: 2px;
    text-align: center;
    width: 25px;
}

.accordion-group .accordion-item .accordion-body {
    font-size: 13px;
}

.accordion-group .accordion-item .accordion-body p {
    margin-top: 10px;
}

/* 06.6 Panels
   ---------------------------------------------------------------------------*/
.panel-group .panel,
.panel {
    box-shadow: none;
    border: none;
    border-radius: 0;
    margin: 0;
}

.panel-heading {
    padding: 18px;
    border-radius: 0;
    border: 0;
}

.panel-heading a:focus {
    outline: none;
}

.panel-heading a small {
    font-style: italic;
    text-transform: none;
    font-weight: 400;
    font-size: 80%;
    padding: 0 0 0 10px;
}

.panel-heading a i {
    padding: 0 0 0 10px;
}

.panel-naked {
    background: none;
}

.panel-naked .panel-heading {
    padding: 0;
}

.panel-naked .panel-body {
    padding: 30px 0 0 0;
}

.panel-group .panel-heading + .panel-collapse .panel-body {
    border: 0;
}

.panel-body {
    padding: 30px;
}

/* 07. Main Navigation
   ---------------------------------------------------------------------------*/
.navbar-header {
    position: relative;
    z-index: 100;
}

.yamm .dropdown {
    position: relative;
}

.yamm .yamm-fw {
    position: static;
}

.navbar {
    border: 0;
    margin: 0;
    transition: all .4s;
}

.navbar .navbar-brand > img {
    transition: all .4s;
    /*max-height: 41px;*/
}

.navbar .search_button {
    position: absolute;
    right: 0;
    top: 0;
    padding: 4px 6px;
    z-index: 1;
}

.navbar .search_button:focus {
    outline: none;
}

.navbar .search_button:after {
    position: absolute;
    top: -60px;
    right: -60px;
    height: 100px;
    width: 100px;
    content: '';
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    z-index: -1;
}

.navbar .dropdown-submenu {
    position: relative;
}

.navbar .dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: 0;
    border-radius: 0;
}

.navbar .dropdown-submenu:hover > .dropdown-menu {
    display: block;
}

.navbar .dropdown-submenu > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 4px 0 4px 4px;
    margin-top: -4px;
    margin-right: 0;
    position: absolute;
    right: 0;
    top: 50%;
}

.navbar .dropdown-submenu.pull-left {
    float: none;
}

.navbar .dropdown-submenu.pull-left > .dropdown-menu {
    left: -100%;
    margin-left: 10px;
    border-radius: 0;
}

.navbar .dropdown-menu {
    min-width: 220px;
    margin: 0;
    padding: 0;
    border: 0;
    border-radius: 0;
    border-top: 6px solid transparent;
}

.navbar .dropdown-menu > li {
    padding: 0 15px;
}

.navbar .dropdown-menu > li > a {
    padding: 0 10px 0 0;
    font-size: 0.9em;
    line-height: 42px;
    text-transform: uppercase;
    position: relative;
}

.navbar .dropdown-menu > li > a:hover,
.navbar .dropdown-menu > li > a:focus {
    background: transparent;
}

.navbar .dropdown-menu > li:last-of-type {
    border: 0;
}

.navbar .dropdown-menu.widget-box {
    padding: 20px;
    width: 280px;
}

.navbar .dropdown-menu > .active > a,
.navbar .dropdown-menu > .active > a:hover,
.navbar .dropdown-menu > .active > a:focus {
    background: transparent;
}

.navbar-toggle {
    background: none;
    border: 0;
    border-radius: 0;
    padding: 0;
    margin: 25px 12px 0;
    float: left;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .4s;
}

.navbar-toggle:hover,
.navbar-toggle:focus {
    background: none;
}

.navbar-nav > li > a {
    line-height: 41px;
    text-transform: uppercase;
    font-size: 0.8em;
    font-weight: 300;
    padding: 12px 4px 15px 4px;
    border-top: 3px solid transparent;
    background: none;
    letter-spacing: 1px;
}

.navbar-nav > li > a:hover,
.navbar-nav > li > a:focus {
    background: none;
}

.nav .open > a:hover,
.nav .open > a:focus,
.nav .open > a {
    background: none;
}

.nav .open .dropdown-toggle:hover,
.nav .open .dropdown-toggle:focus,
.nav .open .dropdown-toggle {
    background-color: transparent;
    opacity: 1;
}

.opaque .navbar-nav > li > a {
    padding-top: 5px;
    padding-bottom: 8px;
}

.opaque .navbar-brand {
    margin-top: -7px;
    padding-bottom: 12px;
}

.opaque .navbar-brand > img {
    /*height: 33px;*/
}

.opaque .navbar-toggle {
    margin-top: 20px;
}

.navbar-collapse.collapse {
    border: none;
    display: block;
    position: fixed;
    z-index: 50;
    left: -100%;
    top: 0;
    bottom: 0;
    padding-top: 55px;
    width: 100%;
    max-height: none;
    transition: left 0.5s ease;
}

.navbar-collapse.collapse.in {
    left: 15px;
}

.shopping_cart_dropdown {
    text-align: right;
}

.shopping_cart_dropdown .product_list_widget {
    padding: 0;
    margin: 0;
    list-style: none;
}

.shopping_cart_dropdown .product_list_widget li {
    display: block;
    clear: both;
    overflow: hidden;
    margin: 0 0 13px 0;
    padding: 0 20px 13px 0;
    position: relative;
    line-height: 22px;
}

.shopping_cart_dropdown .product_list_widget li .delete {
    position: absolute;
    top: 0;
    right: 0;
}

.shopping_cart_dropdown .product_list_widget li a > img {
    display: block !important;
    float: left;
    max-width: 60px;
    padding: 0 17px 0 0;
}

.shopping_cart_dropdown .product_list_widget li .quantity {
    display: block;
    margin-top: 0;
}

.shopping_cart_dropdown .total {
    padding: 0 20px 0 0;
    display: block;
}

.shopping_cart_dropdown .total h6 {
    margin-bottom: 0;
}

.shopping_cart_dropdown .total span {
    font-size: 1.6em;
    line-height: 30px;
    font-weight: 600;
}

.shopping_cart_dropdown .action-button .btn {
    margin: 15px 0 0 8px;
}

/* 07.1 Standard Navigation
   ---------------------------------------------------------------------------*/
.navbar-standard {
    background: transparent;
}

.navbar-standard .navbar-brand .logo-white {
    display: block;
}

.navbar-standard .navbar-brand .logo-dark {
    display: none;
}

.navbar-standard.opaque .navbar-brand .logo-white {
