    left: 0;
    right: 0;
    padding: 10px 20px;
}

.category-banner .category-content .category-title {
    margin: 0;
}

/* 15. Widgets
   ---------------------------------------------------------------------------*/
.widget-title {
    margin: 0;
}

.widget-title-bordered {
    margin: 0 0 30px 0;
    padding: 0 0 20px 0;
}

.widget {
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
    display: block;
    overflow: hidden;
}

.widget .btn {
    border-radius: 4px;
}

.widget.widget-media .media:last-of-type {
    margin: 0;
    padding: 0;
    border: 0;
}

.widget.widget-media .media .media-object {
    max-width: 72px;
}

.widget.widget-media.rounded .media-body {
    margin-top: 10px;
}

.widget.widget-project {
    margin: 0 -9px -9px 0;
}

.widget.widget-project li {
    width: 30%;
    float: left;
    margin: 0 9px 9px 0;
}

.widget .media-bordered {
    padding-bottom: 15px;
    margin: 0 0 15px 0;
}

.widget .media a {
    display: block;
    color: inherit;
}

.widget .media .product-detail {
    margin: 0;
}

.widget .media .product-detail .rate {
    margin: 8px 0 4px 0;
}

.widget .media .product-detail .rate .fa {
    padding: 0 3px 0 0;
}

.widget .cat-item {
    padding-bottom: 10px;
    margin-bottom: 10px;
}

.widget .cat-item a {
    display: block;
}

.widget .cat-item:last-of-type {
    padding: 0;
    margin: 0;
    border-bottom: 0;
}

.filter_price_content .slider.slider.slider-horizontal {
    width: 100%;
}

.filter_price_content .slider .slider-track .slider-handle {
    opacity: 1;
    filter: alpha(opacity=100);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.filter_price_content .slider .slider-track .slider-selection {
    box-shadow: none;
}

/* 16. Twitter
   ---------------------------------------------------------------------------*/
.twitter-post .post p {
    margin: 30px 30px 0 30px;
    font-size: 2.4em;
}

/* 17. Forms
   ---------------------------------------------------------------------------*/
.form-control {
    box-shadow: none;
    border-radius: 0;
}

.form-control:focus {
    box-shadow: none;
}

.form-control.input-lg {
    resize: none;
    font-size: 0.9em;
}

.form-control.rounded {
    border-radius: 4px;
}

label {
    font-weight: normal;
}

label.error {
    font-size: 0.9em;
    margin: 5px 0 0 0;
}

.form-inline button {
    margin: 0 10px 0 0;
}

.newsletter-form {
    position: relative;
}

.newsletter-form input {
    border: 0;
    height: 72px;
    border-radius: 40px;
}

.newsletter-form input.input-lg {
    font-size: 1.4em;
}

.newsletter-form input:focus {
    box-shadow: none;
}

.newsletter-form button {
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
    background: none;
}

.h_search_form {
    overflow: hidden;
    height: 0;
}

.h_search_form .h_search_form_wrapper {
    position: relative;
    padding: 10px 0;
}

.h_search_form .btn {
    background: transparent;
    border: none;
    padding-left: 12px;
    padding-right: 12px;
}

.h_search_form .form-control {
    background-color: transparent;
    border: none;
    box-shadow: none;
}

.h_search_form .h_search_close {
    position: absolute;
    right: 0;
    top: 0;
    line-height: 54px;
    z-index: 2;
}

.theme-form-group {
    padding: 2px;
    border-radius: 30px;
    line-height: 42px;
}

.theme-form-group .btn {
    padding-left: 12px;
    padding-right: 12px;
}

.theme-form-group .form-control {
    background-color: transparent;
    border: none;
    box-shadow: none;
}

.theme-form-group .form-control:first-child,
.theme-form-group .input-group-addon:first-child,
.theme-form-group .input-group-btn:first-child > .btn,
.theme-form-group .input-group-btn:first-child > .dropdown-toggle {
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
}

.theme-form-group .form-control:last-child,
.theme-form-group .input-group-addon:last-child,
.theme-form-group .input-group-btn:last-child > .btn,
.theme-form-group .input-group-btn:last-child > .dropdown-toggle {
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
}

/* 18. Pagination
   ---------------------------------------------------------------------------*/
.pager {
    margin: 0;
}

.pager li {
    padding: 0 3px;
}

.pager li > a,
.pager li > span {
    padding: 0 10px;
    margin: 0;
    min-width: 36px;
    height: 36px;
    line-height: 30px;
    text-transform: uppercase;
    font-size: 1em;
    border-radius: 4px;
}

.pager.circle li > a,
.pager.circle li > span {
    padding: 0;
    width: 36px;
    border-radius: 50%;
}

.pager.custom li {
    display: inline-block;
    vertical-align: middle;
}

.pager.custom li > a,
.pager.custom li > span {
    padding: 0 45px;
    border: 0;
    background: none;
    height: auto;
    line-height: 1;
    width: auto;
    min-width: auto;
}

/* 19. Footer
   ---------------------------------------------------------------------------*/
#footer small {
    display: block;
    font-size: 90%;
}

#footer .logo {
    max-height: 41px;
}

#footer .widget-title {
    margin: 0;
}

#footer ul {
    margin: 0;
    padding-left: 0;
    list-style: none;
}

#footer .widget-post li {
    position: relative;
    padding: 0 0 20px 50px;
}

#footer .widget-post li .date-post {
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    display: block;
    padding: 7px;
    line-height: 13px;
}

#footer .widget-post li .date-post small {
    display: block;
    font-size: 70%;
}

#footer .widget-post li .title-post {
    display: block;
}

#footer .widget-address li {
    position: relative;
    padding: 0 0 15px 32px;
}

#footer .widget-address li i {
    position: absolute;
    top: 5px;
    left: 0;
    width: 25px;
    display: block;
    text-align: center;
}

#footer .widget-tag {
    margin: 0 -5px -5px 0;
}

#footer .widget-tag .btn {
    margin: 0 5px 5px 0;
    border-width: 1px;
}

#footer .widget_archive li {
    margin: 0 0 10px 0;
    padding: 0 0 10px 0;
}

#footer .widget_archive li:last-of-type {
    margin: 0;
    padding: 0;
    border-bottom: 0;
}

#footer .flickr_container {
    margin: 0 -15px -15px 0;
}

#footer .flickr_container .flickr_badge_image {
    float: left;
    margin: 0 15px 15px 0;
}

#footer .flickr_container .flickr_badge_image a:hover {
    opacity: 0.6;
    filter: alpha(opacity=60);
}

#footer .form-control {
    font-size: 0.9em;
}

#footer .form-control:focus {
    box-shadow: none;
}

/* 20. Error Pages
   ---------------------------------------------------------------------------*/
.error-404:before,
.error-500:before {
    font-size: 650px;
    font-weight: bold;
    left: -150px;
    position: absolute;
    top: -300px;
    -webkit-transform: rotate(-30deg);
    transform: rotate(-30deg);
    z-index: 0;
}

.error-404:before {
    content: "404";
}

.error-500:before {
    content: "500";
}

/* 21. Floating elements
   ---------------------------------------------------------------------------*/
#back_to_top {
    display: none;
    position: fixed;
    z-index: 1000;
    bottom: 40px;
    right: 40px;
}

#back_to_top a {
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center;
    outline: none;
    border-radius: 50%;
    opacity: 0.5;
    filter: alpha(opacity=50);
}

#back_to_top a:hover {
    opacity: 0.8;
    filter: alpha(opacity=80);
}

#load {
    background-color: #ffffff;
    height: 100%;
    overflow: hidden;
    position: fixed;
    width: 100%;
    z-index: 99999;
}

.browsehappy {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10000;
    padding: 30px;
    margin: 0;
    text-align: center;
}

.browsehappy a {
    text-decoration: underline;
}

.browsehappy a:hover {
    text-decoration: none;
}
