/* SHOWCASE BUTTONS AND FILTERS START */
/* ---- button ---- */
.button {
  display: inline-block;
  padding: 0.5em 0.5em;
  /*background: #EEE;*/
  border: none;
  border-radius: 3px;
  /*background-image: linear-gradient( to bottom, hsla(0, 0%, 0%, 0), hsla(0, 0%, 0%, 0.2) );*/
  color: white;
  font-size: 1.3em;
  /*text-shadow: 0 1px white;*/
  cursor: pointer;
  outline: 0;
  background: transparent;
  letter-spacing: 1px;
}

.button:hover {
  background-color: #0986c2;
  color: white;
}

.button:active,
.button.is-checked {
  background-color: #0986c2;
}

.button.is-checked {
  color: white;
  text-shadow: 0 -1px rgba(0, 0, 0, 0.8);
  box-shadow: inset 0 1px 10px rgba(0, 0, 0, 0.8);
}

.button:active {
  box-shadow: inset 0 1px 10px rgba(0, 0, 0, 0.8);
}

/* ---- button-group ---- */
.button-group {
  text-align: center;
}

.button-group:after {
  content: '';
  display: block;
  clear: both;
}

.button-group .button {
  border-radius: 1.5em;
}

.button-group .button {
  border-radius: 1.5em;
}

/* ---- isotope ---- */
.isotope {
  /*border: 1px solid #333;*/
}

/* clear fix */
.isotope:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- .element-item ---- */
.element-item {
  position: relative;
