/* Start General Class */
body {
  color: #909090;
}

body.boxed-layout .page {
  background-color: #ffffff;
}

a {
  color: #909090;
}

a:active,
a:focus,
a:hover,
a.light:hover,
a.dark:hover {
  color: #0986c2;
}

.text-primary,
.text-primary:hover {
  color: #0986c2;
}

h1,
h2,
h3,
h4,
h5,
h6,
.contact-form label,
dl.metadata dt {
  color: #222222;
}

.light {
  color: #ffffff;
}

.light h1,
.light h2,
.light h3,
.light h4,
.light h5,
.light h6,
.light .contact-form label,
.light p {
  color: #ffffff;
}

.dark {
  color: #222222;
}

.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark h5,
.dark h6,
.dark .contact-form label,
.dark p {
  color: #222222;
}

.primary {
  color: #0986c2;
}

.primary h1,
.primary h2,
.primary h3,
.primary h4,
.primary h5,
.primary h6,
.primary .h1,
.primary .h2,
.primary .h3,
.primary .h4,
.primary .h5,
.primary .h6,
.primary .contact-form label,
.primary p {
  color: #0986c2;
}

.label-primary,
.bg-primary {
  background: #0986c2;
}

::-moz-selection,
::-moz-selection {
  background: #0986c2;
  color: #ffffff;
}

::selection,
::-moz-selection {
  background: #0986c2;
  color: #ffffff;
}

p strong {
  color: #222222;
}

.demo-1 .sl-slide .sl-slide-inner,
.demo-2 .sl-slide .sl-slide-inner {
  background-repeat: no-repeat;
  background-size: cover;
}

.demo-1 .sl-slide .main-title,
.demo-2 .sl-slide .main-title {
  color: #ffffff;
}

.demo-1 .sl-slide blockquote p,
.demo-2 .sl-slide blockquote p {
  color: #ffffff;
}

.owl-theme .owl-controls .owl-page span {
  background: #222222;
}

.owl-theme.pagination-light .owl-page span {
  background: #ffffff;
}

.section-title span {
  color: #0986c2;
}

.section-gray {
  background-color: #eeeeee;
}

.section-gray .indicator .indicator-line {
  border-top: 1px solid #dddddd;
  background-image: linear-gradient(90deg, #dddddd 1px, transparent 1px);
}

.section-shade {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5) 0, #000000 130%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#ff000000', GradientType=0);
}

.section-shade-half {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 0, #000000 130%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#ff000000', GradientType=0);
}

.parallax {
  background-color: #222222;
}

.rating-input i,
.rate i {
  color: #0986c2;
}

.price-shop ins {
  color: #222222;
}

.table .evidence td {
  background: #0986c2;
  color: #ffffff;
}

.table .evidence td h5 {
  color: #ffffff;
}

.table-bordered > thead > tr > th,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > td {
  border-color: #eeeeee;
}

/* End General Class */
/* Start Bootstrap-Touchspin Class */
.bootstrap-touchspin .btn {
  border: 1px solid #eeeeee;
  color: #909090;
  background: none;
}

.bootstrap-touchspin .btn:hover {
  color: #222222;
  background: none;
}

.bootstrap-touchspin input.qty {
  border: 1px solid #eeeeee;
  background: #ffffff;
}

.bootstrap-touchspin input.qty:focus {
  color: #909090;
}

/* End Bootstrap-Touchspin Class */
